//////////////////////
// drop
//////////////////////

.drop-outer {
  display: inline-block;
  position: relative;
  // z-index
  &.active {
    &, > .drop {
      z-index: @index-drop;
    }
  }
  &.out {
    &, > .drop {
      z-index: @index-drop - 1;
    }
  }
  // content
  > .xt-backdrop, > .drop {
    .display(none);
  }
  &.active, &.out {
    > .xt-backdrop, > .drop {
      .display(block);
    }
  }
}

.drop {
  // styles
  position: absolute;
  > .drop-inner {
    position: relative;
    z-index: @index-base; // fix ::before negative z-index
  }
  // others
  &.out {
    pointer-events: none;
  }
}

// addons

.drop-outer-static {
  position: static;
  > .drop,
  > .drop > .drop-inner {
    width: 100%;
  }
}

// before + after

.drop-before() {
  padding: @drop-space;
  left: auto;
  right: 100%;
}

.drop.drop-before {
  .drop-before() !important;
}

.drop-after() {
  padding: @drop-space;
  left: 100%;
  right: auto;
}

.drop.drop-after {
  .drop-after() !important;
}

// left + right

.drop-left() {
  padding: @drop-space 0;
  left: 0;
  right: auto;
}

.drop.drop-left {
  .drop-left() !important;
}

.drop-right() {
  padding: @drop-space 0;
  left: auto;
  right: 0;
}

.drop.drop-right {
  .drop-right() !important;
}

// bottom + top

.drop-bottom() {
  padding: @drop-space 0;
  top: 100%;
  bottom: auto;
}

.drop.drop-bottom {
  .drop-bottom() !important;
}

.drop-top() {
  padding: @drop-space 0;
  top: auto;
  bottom: 100%;
}

.drop.drop-top {
  .drop-top() !important;
}

// center

.drop-center() {
}

.drop.drop-center {
  .drop-center() !important;
}

// middle

.drop-middle() {
  padding-top: 0;
  padding-bottom: 0;
}

.drop.drop-middle {
  .drop-middle() !important;
}

.drop-middle-bottom() {
  top: auto;
  bottom: 0;
}

.drop.drop-middle-bottom {
  .drop-middle-bottom() !important;
}

.drop-middle-top() {
  top: 0;
  bottom: auto;
}

.drop.drop-middle-top {
  .drop-middle-top() !important;
}

// .drop-disable

.drop-disable {
  position: static;
  z-index: initial;
  top: auto;
  left: auto;
  bottom: auto;
  right: auto;
  > a, > button {
    .display(none) !important;
  }
  > .drop {
    .display(block) !important;
    position: relative;
    > .drop-inner {
      z-index: initial;
      min-width: auto;
      &@{drop-design} {
        display: none;
      }
    }
  }
}
